import { Tag } from "antd";
import { useCallback, useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { Fn } from "../../../../../../helper/function";
import { PaginationRequest, TableColumn } from "../../../../../../models/baseModel";
import { ApiPublicService } from "../../../../../../services/taskService";
import { MyBox } from "../../../../../core/MyBox";
import { MyButton } from "../../../../../core/MyButton";
import { OrderByModel, PageInfoModel } from "../../../../../core/TableModel";
import TableComponent from "../../../../../core/TableNewComponent";
import { UIFormatDateTimeVNComponent } from "../../../../../core/UI_FormatComponent";

export default function ManageIndexComponent() {
    const nav = useNavigate();
    const projectKey = useParams().projectKey;
    const [pageInfo, setPageInfo] = useState<PageInfoModel>(new PageInfoModel());
    const [orderBy, setOrderBy] = useState<OrderByModel>(new OrderByModel());
    const [data, setData] = useState([]);
    const [isLoading, setIsLoading] = useState(false);

    const column: any = [
        new TableColumn({
            title: "STT", key: "stt", render: (data: any, row: any, index: number) =>
                Fn.RenderSTT(data, row, index, pageInfo),
        }),
        new TableColumn({
            title: "Tên", key: "name", render: (data: string, row: any) =>
                <MyButton value={data} type="link" onClick={() => {
                    handleClickDetail(row.key);
                }} />
        }),
        new TableColumn({
            title: "Trạng thái", key: "status", render: (data: number) => {
                const val = data === 0 ? "Chưa xuất bản" : "Đã xuất bản";
                const color = data === 0 ? "red" : "blue";
                return <Tag color={color}>{val}</Tag>
            }
        }),
        new TableColumn({
            title: "Ngày xuất bản", key: "lastPublishAt", render: (data: string) =>
                <UIFormatDateTimeVNComponent value={data} />
        }),
        new TableColumn({
            title: "Ngày xuất bản", key: "createdAt", render: (data: string) =>
                <UIFormatDateTimeVNComponent value={data} />
        }),
        new TableColumn({
            title: "Ngày xuất bản", key: "updatedAt", render: (data: string) =>
                <UIFormatDateTimeVNComponent value={data} />
        })
    ];

    const loadData = useCallback(() => {
        setIsLoading(true);
        var paginationRequest: PaginationRequest = new PaginationRequest(pageInfo.pageNumber, pageInfo.pageSize, orderBy);
        ApiPublicService.GetList(paginationRequest, projectKey)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    setPageInfo(new PageInfoModel(rs));
                    setData(rs.data.pagedData)
                }
            })
            .finally(() => {
                setIsLoading(false);
            })
    }, [pageInfo.pageNumber, pageInfo.pageSize, projectKey, orderBy])

    useEffect(() => {
        loadData();
    }, [loadData])

    function handleClickDetail(key: string | undefined) {
        nav(`/tools/tasks/projects/${projectKey}/codeManage/document/publish/edit/${key}`);
    }

    function handleClickAdd() {
        nav(`/tools/tasks/projects/${projectKey}/codeManage/document/publish/add`);
    }

    return (
        <MyBox
            title="Public API"
            isLoading={isLoading}
            buttonList={() => (
                <MyButton
                    type="primary"
                    value="Thêm mới"
                    onClick={handleClickAdd} />
            )}>

            <TableComponent
                pageInfo={pageInfo}
                setPageInfo={setPageInfo}
                orderBy={orderBy}
                setOrderBy={setOrderBy}
                loadData={loadData}
                dataSource={data}
                column={column}
                loading={isLoading} />

            {/* <ModifyManageComponent
                projectKey={projectKey}
                loadData={loadData}
                visible={visible}
                setVisible={setVisible}
                isLoading={isLoading}
                setIsLoading={setIsLoading} />

            <ModifyManageComponent
                projectKey={projectKey}
                loadData={loadData}
                visible={visibleEdit}
                setVisible={setVisibleEdit}
                keyEdit={keyEdit}
                setKeyEdit={setKeyEdit}
                isLoading={isLoading}
                setIsLoading={setIsLoading} /> */}
        </MyBox>
    )
}